<template>
  <div class="col-3">
    <div class="panel">
      <div class="panel-heading">
        <div class="panel-title">案件来源分析</div>
      </div>
      <div class="panel-body" v-loading="loading" style="overflow: auto;">
        <div class="chart" id="chart3"></div>
      </div>
    </div>
  </div>
</template>

<script>
import watchDate from '@/mixins/watchDate'
export default {
  props: ['dateType'],
  data () {
    return {
      loading: false
    }
  },
  mixins: [
    watchDate
  ],
  mounted () {
    this.init()
  },
  methods: {
    init () {
      var self = this
      this.loading = true
      this.$http.get({
        api: 'A_S_CASE_SOURCE',
        params: {
          dateType: this.dateType
        },
        loading: this.$data,
        callback: (res) => {
          self.loading = false
          self.draw(res)
        }
      })
    },
    draw (a) {
      let option = {
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        series: [{
          name: '数量',
          type: 'pie',
          radius: '70%',
          center: ['50%', '50%'],
          data: a,
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: 'rgba(0, 0, 0, 0.5)'
            }
          }
        }]
      }
      let myChart = this.$echarts.init(document.getElementById('chart3'), 'macarons')
      myChart.setOption(option)
    }
  }
}

</script>



